.projects {
    background: $background-grey;
    padding: 4rem 3rem;
    max-width: 1440px;

    @media (max-width: $mobile) {
        padding: 2rem 2rem 4rem 2rem;
    }

    &__title {
        color: $title-color;
        font-size: 2.25rem;
        line-height: 2.75rem;
        padding: 1rem 0;

        @media(max-width: $mobile) {
            text-align: center;
        }
    }

    &__description {
        color: $description-color;
        font-weight: 300;
        font-size: 1.25rem;
        line-height: 2.25rem;
        max-width: 681px;
        max-height: 108px;
        text-align: justify;
    }
}

.container {
    &__projects {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-column-gap: 2rem;
        grid-row-gap: 2rem;
        padding: 0;

        @media (max-width: $mobile) {
            grid-template-columns: 1fr;
            grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
        }
    }

    .button {
        display: block;
        padding: 2rem 2rem 0 2rem;
        text-align: center;
        width: 100%;


        &__load {
            background: $background-grey;
            border: 2px solid $border;
            border-radius: .3rem;
            cursor: pointer;
            font-size: 1.2rem;
            font-weight: 600;
            padding: 1rem 3rem;
            transition: .3s linear;
            text-transform: uppercase;

            @media(min-width: $mobile) {
                &:hover {
                    background: $border;
                    color: $background-grey;
                }
            }
        }
    }
}

.project {
    background: #FFFFFF;
    box-shadow: 0px 6px 16px rgba(24, 41, 67, 0.09);
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    overflow: hidden;

    &__name {
        color: $border;
        font-size: 1.25rem;
        line-height: 2rem;
        margin: 0;
        padding: .5rem 0;
    }

    &__description {
        color: $item-color;
        font-size: 1.1rem;
        font-weight: 300;
        line-height: 2rem;
        margin: 0;
    }

    &__wrapper {
        padding: 2rem;
    }

    &__footer {
        background: $background-grey;
        padding: 1rem 2rem;
        text-align: end;
        width: 100%;
    }

    &__link {
        align-items: center;
        color: $blue;
        display: inline-flex;
        font-size: 1.1rem;
        font-weight: 500;
        line-height: 2rem;
        padding: 0 1rem;
        text-decoration: none;

        &:nth-child(2) {

            position: relative;

            &::after {
                background: #CBD2D9;
                content: "";
                display: block;
                height: 2rem;
                left: -2%;
                pointer-events: none;
                position: absolute;
                width: 2px;
            }
        }

        &::before {
            background: url("../assets/img/Code.svg");
            background-position: center;
            background-repeat: no-repeat;
            content: "";
            display: block;
            height: 24px;
            margin: 0 .5rem;
            width: 24px;
        }

        &--active::before {
            background: url("../assets/img/Demo.svg");
            background-position: center center;
            background-repeat: no-repeat;
        }
    }
}